import React from "react";
import Countdown from "react-countdown";
import Anchor from "../../../components/Anchor";
import '../style/FreeMintCountDown.css';

const CountDown = () => {
  const revealDate = new Date("March 9, 2023 15:00:00 UTC").getTime();
  const fivePmUtc = new Date("March 9, 2023 17:00:00 UTC").getTime();
  const nowTime = new Date().getTime();

  if (nowTime < revealDate) {
    return (
      <div id={'countdown'}>
        <Countdown
          date={revealDate}
          renderer={({days, hours, minutes, seconds}) => {
            return (
              <div className="countdown-internal">
                <span className={'timer'}>
                  Free mint for People of Eden Citizenship starts in {String(hours).length === 1 ? `0${String(hours)}` : String(hours)} hrs: {String(minutes).length === 1 ? `0${String(minutes)}` : String(minutes)} mins: {String(seconds).length === 1 ? `0${String(seconds)}` : String(seconds)} secs
                  <Anchor href={'https://mahastarter.com/#/tiers/people-of-eden'} className={'links'}
                          trackingid={'free-mint'} target={'_blank'}> here</Anchor>
                </span>
              </div>
            )
          }}
        />
        <br/><br/>
      </div>
    )
  }

  if (nowTime > revealDate) {
    if (nowTime < fivePmUtc) {
      return (
        <div id={'countdown'}>
          <Countdown
            date={fivePmUtc}
            renderer={({days, hours, minutes, seconds}) => {
              return (
                <div className="countdown-internal">
                  <span className={'timer'}>
                    People of Eden Citizen's would be revealed in {String(hours).length === 1 ? `0${String(hours)}` : String(hours)} hrs: {String(minutes).length === 1 ? `0${String(minutes)}` : String(minutes)} mins: {String(seconds).length === 1 ? `0${String(seconds)}` : String(seconds)} secs
                    <Anchor href={'https://opensea.com/collection/people-of-eden'} className={'links'}
                            trackingid={'free-mint'} target={'_blank'}> here</Anchor>
                  </span>
                </div>
              )
            }}
          />
          <br/><br/>
        </div>
      )
    } else {
      return (
        <div id={'countdown'}>
          <div className="countdown-internal">
              <span className={'timer'}>
                People of Eden Citizen's are live <Anchor href={'https://mahastarter.com/#/tiers/people-of-eden'}
                                                          className={'links'}
                                                          trackingid={'free-mint'} target={'_blank'}> here</Anchor>
              </span>
          </div>
          <br/><br/>
        </div>
      )
    }
  }

  return (
    <div/>
  )
}

export default CountDown;
